import { _decorator, Component, Label, Sprite, ImageAsset, Texture2D, SpriteFrame } from 'cc';
import { TelegramWebApp } from './telegram-web';

const { ccclass, property } = _decorator;

@ccclass('UserInfoDisplay')
export class UserInfoDisplay extends Component {
    @property(Label)
    public userInfoLabel: Label | null = null;

    @property(Label)
    public avatarUrlLabel: Label | null = null;

    @property(Sprite)
    public userAvatar: Sprite | null = null;

    start() {
        this.updateUserInfo();
    }

    private async updateUserInfo() {
        const tgWebApp = TelegramWebApp.Instance;
        await tgWebApp.init();

        const user = tgWebApp.getTelegramUser();
        if (user) {
            if (this.userInfoLabel) {
                this.userInfoLabel.string = `ID: ${user.id}\nUsername: ${user.username}`;
            }

            if (this.avatarUrlLabel) {
                this.avatarUrlLabel.string = `${user.photo_url}`;
            }

            if (user.photo_url && this.userAvatar) {
                this.loadAvatar(user.photo_url);
            }
        } else {
            console.error("User data is not available.");
        }
    }

    private loadAvatar(url: string) {
        if (!this.userAvatar) return;

        const imgAsset = new ImageAsset();

        const img = new Image();
        img.crossOrigin = 'anonymous';
        img.src = url;

        img.onload = () => {
            imgAsset.reset(img);

            const texture = new Texture2D();
            texture.image = imgAsset;

            texture.setFilters(Texture2D.Filter.LINEAR, Texture2D.Filter.LINEAR);

            const spriteFrame = new SpriteFrame();
            spriteFrame.texture = texture;

            this.userAvatar.type = Sprite.Type.SIMPLE;
            this.userAvatar.sizeMode = Sprite.SizeMode.RAW;
            this.userAvatar.spriteFrame = spriteFrame;
        };

        img.onerror = () => {
            console.error("Failed to load avatar image.");
        };
    }
}
