import { _decorator, Component, instantiate, Node, Prefab } from 'cc';
import { TaskData, TaskItem, TaskType } from './TaskItem';
const { ccclass, property } = _decorator;

@ccclass('TasksScrollViewer')
export class TasksScrollViewer extends Component {
    @property(Prefab)
    taskItemPrefab: Prefab = null;

    @property(Node)
    contentNode: Node = null;

    testTasks: TaskData[] = [];

    start() {

        //only for test
        this.testTasks = [
            {
                taskType: TaskType.Other,
                isCompleted: false,
                taskId: "task_001",
                taskName: "Watch the video",
                reward: 100,
                channelLink: "https://www.youtube.com/watch?v=dQw4w9WgXcQ",
            },
            {
                taskType: TaskType.Subscribe,
                isCompleted: false,
                taskId: "task_002",
                taskName: "Subscribe to channel",
                reward: 200,
                channelLink: "https://t.me/hamster_hotel",
            },
            {
                taskType: TaskType.MoneyCount,
                isCompleted: true,
                taskId: "task_003",
                taskName: "Collect 5000 $SEM",
                reward: 500,
                moneyCount: 5000,
            },
            {
                taskType: TaskType.FriendsCount,
                isCompleted: false,
                taskId: "task_004",
                taskName: "Invite 5 friends",
                reward: 500,
                friendsCount: 5,
            }
        ];

        this.init(this.testTasks);
    }

    public init(tasks: TaskData[]){
        if(this.taskItemPrefab && this.contentNode){
            for (let i = 0; i < tasks.length; i++) {
                const newTaskItemPrefab = instantiate(this.taskItemPrefab);
                this.contentNode.addChild(newTaskItemPrefab);
                newTaskItemPrefab.getComponent(TaskItem).init(tasks[i]);
            }
        }
    }
}

