import { _decorator, Component, Node } from 'cc';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;
import { native } from "cc"; 
const { copyTextToClipboard } = native;

@ccclass('CopyInviteLink')
export class CopyInviteLink extends Component {

    @property(String)
    public baseLinkToShare: String = 'https://t.me/hamsmining_bot/start?startapp=';

    start() {
        this.node.on(Node.EventType.TOUCH_END, this.onClick, this);
    }

    async onClick() {
        const tgWebApp = TelegramWebApp.Instance;
        await tgWebApp.init();
    
        const user = tgWebApp.getTelegramUser();
        const urlForCopy = this.baseLinkToShare + user.id.toString();
    
        try {
            await navigator.clipboard.writeText(urlForCopy);
        } catch (err) {
            console.error('Cant copy text with: ', err);
        }
    }    
}

