import { _decorator, CCInteger, Component, instantiate, Node, Prefab } from 'cc';
import { FriendItem } from './FriendItem';
const { ccclass, property } = _decorator;

@ccclass('FriendsScrollViewer')
export class FriendsScrollViewer extends Component {

    @property
    numberOfFriends: number = 5;

    @property(Prefab)
    friendItemPrefab: Prefab = null;

    @property(Node)
    contentNode: Node = null;

    start() {
        //get all friends and init
        this.init();
    }

    public init(/*/initing data/*/){
        if(this.friendItemPrefab && this.contentNode){
            for (let i = 0; i < this.numberOfFriends; i++) {
                const newFriendItemPrefab = instantiate(this.friendItemPrefab);
                this.contentNode.addChild(newFriendItemPrefab);
                newFriendItemPrefab.getComponent(FriendItem).init("friend num "+ i.toString());
            }
        }
    }
}

