import { _decorator, Component, Node } from 'cc';
import { TelegramWebApp } from './telegram-web';
const { ccclass, property } = _decorator;

@ccclass('ShareInviteLink')
export class ShareInviteLink extends Component {

    @property(String)
    public baseLinkToShare: String = 'https://t.me/hamsmining_bot/start?startapp=';

    start() {
        this.node.on(Node.EventType.TOUCH_END, this.onClick, this);
    }

    async onClick() {
        const tgWebApp = TelegramWebApp.Instance;
        await tgWebApp.init();

        const user = tgWebApp.getTelegramUser();
        const urlForShare = this.baseLinkToShare + user.id.toString();

        tgWebApp.share(urlForShare, 'Hi! \nJoin me in the Hamster Hotel Mining game! \nPack your bags and accumulate a $SEM token for your future hotel!');
    }
}

