import { _decorator, Button, Component, Enum, Label, Node, Sprite, SpriteFrame } from 'cc';
import { TelegramWebApp } from './telegram-web';
import { ButtonFeedbacks } from './ButtonFeedbacks';
const { ccclass, property } = _decorator;

export enum TaskType {
    Other = "Other",
    Subscribe = "Subscribe",
    MoneyCount = "MoneyCount",
    FriendsCount = "FriendsCount",
}

export interface TaskData {
    taskType: TaskType;
    isCompleted: boolean;
    taskId: string;
    taskName: string;
    reward: number;

    channelLink?: string;
    moneyCount?: number;
    friendsCount?: number;
}

@ccclass('TaskItem')
export class TaskItem extends Component {

    @property(Label)
    nameLabel: Label | null = null;

    @property(Label)
    rewardLabel: Label | null = null;

    @property(Button)
    goButton: Button | null = null;

    @property(Sprite)
    buttonIconSprite: Sprite | null = null;

    @property(SpriteFrame)
    isCompletedIcon: SpriteFrame | null = null;

    @property(SpriteFrame)
    isNotCompletedIcon: SpriteFrame | null = null;

    @property(Sprite)
    iconSprite: Sprite | null = null;

    @property(SpriteFrame)
    subscribeIcon: SpriteFrame | null = null;

    @property(SpriteFrame)
    moneyIcon: SpriteFrame | null = null;

    @property(SpriteFrame)
    friendsIcon: SpriteFrame | null = null;

    @property(SpriteFrame)
    otherIcon: SpriteFrame | null = null;

    myTaskData: TaskData;

    init(task: TaskData){
        this.myTaskData = task;
        this.updateMe();
    }

    updateTaskData(newTaskData: TaskData){
        this.myTaskData = newTaskData;
        this.updateMe();
    }

    updateMe(){
        if (this.nameLabel) {
            this.nameLabel.string = this.myTaskData.taskName;
        }
        if (this.rewardLabel) {
            this.rewardLabel.string = "+" + this.myTaskData.reward.toString();
        }
        if (this.iconSprite && this.otherIcon && this.subscribeIcon && this.moneyIcon && this.friendsIcon) {
            switch (this.myTaskData.taskType) {
                case TaskType.Other:
                    this.iconSprite.spriteFrame = this.otherIcon;
                    break;
                case TaskType.Subscribe:
                    this.iconSprite.spriteFrame = this.subscribeIcon;
                    break;
                case TaskType.MoneyCount:
                    this.iconSprite.spriteFrame = this.moneyIcon;
                    break;
                case TaskType.FriendsCount:
                    this.iconSprite.spriteFrame = this.friendsIcon;
                    break;
                default:
                    console.log("Unknown task type");
                    break;
            }
        }
        if (this.goButton && this.buttonIconSprite) {
            if (this.myTaskData.isCompleted) {
                if (this.isCompletedIcon) {
                    this.buttonIconSprite.spriteFrame = this.isCompletedIcon;
                }
                if(this.goButton.getComponent(ButtonFeedbacks)) {
                    this.goButton.getComponent(ButtonFeedbacks).enabled = false;
                }
                this.goButton.interactable = false;
            } else {
                if (this.isNotCompletedIcon) {
                    this.buttonIconSprite.spriteFrame = this.isNotCompletedIcon;
                }
                if(this.goButton.getComponent(ButtonFeedbacks)) {
                    this.goButton.getComponent(ButtonFeedbacks).enabled = true;
                }
                this.goButton.interactable = true;
            }

            this.goButton.node.off('click');
            this.goButton.node.on('click', this.onTaskButtonClicked, this);
        }
    }

    async onTaskButtonClicked() {
        if (this.myTaskData.isCompleted) return;

        const isNowCompleted = await this.checkTaskIsComplitedNow();

        if (isNowCompleted) {
            this.myTaskData.isCompleted = true;
            this.updateTaskData(this.myTaskData);
        } else {
            this.goToCompleteTask();
        }
    }

    async checkTaskIsComplitedNow() {
        let isNowCompleted = false;
        switch (this.myTaskData.taskType) {
            case TaskType.Other:
                isNowCompleted = await this.checkOtherIsComplited();
                break;
            case TaskType.Subscribe:
                isNowCompleted = await this.checkSubscribeIsComplited();
                break;
            case TaskType.MoneyCount:
                isNowCompleted = await this.checkMoneyIsComplited();
                break;
            case TaskType.FriendsCount:
                isNowCompleted = await this.checkFriendsIsComplited();
                break;
            default:
                console.log("Unknown task type");
                break;
        }
        return isNowCompleted;
    }

    async goToCompleteTask() {
        switch (this.myTaskData.taskType) {
            case TaskType.Other:
                await this.goOtherComplete();
                break;
            case TaskType.Subscribe:
                await this.goSubscribeComplete();
                break;
            case TaskType.MoneyCount:
                this.goMoneyComplete();
                break;
            case TaskType.FriendsCount:
                this.goFriendsComplete();
                break;
            default:
                console.log("Unknown task type");
                break;
        }
    }

    async delay(ms: number) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    async checkOtherIsComplited() {
        await this.delay(200);
        return true;
    }

    async checkSubscribeIsComplited() {
        await this.delay(200);
        return false;
    }

    async checkMoneyIsComplited() {
        await this.delay(200);
        return true;
    }

    async checkFriendsIsComplited() {
        await this.delay(200);
        return true;
    }

    async goOtherComplete() {
        const tgWebApp = TelegramWebApp.Instance;
        await tgWebApp.init();
        tgWebApp.openTelegramLink(this.myTaskData.channelLink);
    }

    async goSubscribeComplete() {
        const tgWebApp = TelegramWebApp.Instance;
        await tgWebApp.init();
        tgWebApp.openTelegramLink(this.myTaskData.channelLink);
    }

    goMoneyComplete() {
        // go to main screen
    }

    goFriendsComplete() {
        // go to friends screen
    }
}

